<template>
    <glui-select v-model="selected" :disabled="disabled" placeholder="请输入省份名称" :multiple="multiple" :clearable="clearable" filterable remote :remote-method="search" v-on:change="onchange" v-on:clear="clear">
        <glui-option v-for="opt in datas" :key="opt.Id" :label="opt.Name" :value="opt.Id">
        </glui-option>
    </glui-select>
</template>
<script>
import httputils from "@/js/httputils"
export default {
    data: function () {
        return {
            datas: [],
            selected: ""
        };
    },
    props: {
        props: {
            type: [Object],
            default() {
                return {
                    id: "Id",
                    name: "Name"
                }
            }
        },
        defaultitems: {
            type: [Array],
            default() {
                return [];
            }
        },
        value: {
            type: [String, Number, Array],
            default: function () {
                return "";
            }
        },
        isinner: {
            type: Boolean,
            default: function () {
                return true;
            }
        },
        multiple: {
            type: Boolean,
            default: function () {
                return false;
            }
        },
        clearable: {
            type: Boolean,
            default: function () {
                return false;
            }
        },
        disabled: {
            type: Boolean,
            default: function () {
                return false;
            }
        }
    },
    watch: {
        defaultitems(val, old) {
            let that = this;
            if (val != old) {
                let datas = [];
                let selected = that.multiple ? [] : "";
                val && val.forEach(item => {
                    if (that.multiple) {
                        selected.push(item[that.props.id]);
                    } else {
                        selected = item[that.props.id];
                    }
                    datas.push({
                        Id: item[that.props.id],
                        Name: item[that.props.name]
                    });
                });
                that.datas = datas;
                that.selected = selected;
            }
        }
    },
    methods: {
        search: function (keyword, type) {
            var that = this;
            if (!keyword || keyword.length <= 0) {
                return;
            }
            httputils
                .get("/travel/supergoproduct/Common/DomesticProvinces", {
                    key: keyword,
                    isinner: that.isinner,
                    type: type
                })
                .then(function (resp) {
                    if (resp) {
                        that.datas = resp;
                    } else {
                        that.datas = [];
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
        },
        onchange: function (val) {
            let that = this;
            that.$emit("input", val);
            let selected = that.datas.filter(item => {
                if (that.multiple === true) {
                    return val.findIndex(v => v == item.Id) >= 0
                } else {
                    return item && item.Id == val;
                }
            });
            that.$emit("change", val, selected && selected.length == 1 ? selected[0] : selected);
        },
        clear: function () {
            this.datas = [];
        }
    }
}
</script>




