<template>
    <div>
        <glui-form :model="searchForm" label-width="100px">
            <glui-row>
                <glui-col :span="8">
                    <glui-form-item label="出发城市">
                        <glui-col :span='12'>
                            <dropdownprovince v-model='searchForm.ProvinceId' :isinner="true" clearable></dropdownprovince>
                        </glui-col>
                        <glui-col :span='12'>
                            <dropdowncitybyprovince :province='searchForm.ProvinceId' :city.sync="searchForm.CityId" :isinner="true" clearable></dropdowncitybyprovince>
                        </glui-col>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="产品属性">
                        <glui-select v-model="searchForm.ProductAttribute" placeholder="请选择" clearable>
                            <glui-option v-for="item in productAttrs" :key="item.id" :label="item.name" :value="item.id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="所属片区">
                        <glui-select v-model="searchForm.Area" placeholder="请选择" clearable>
                            <glui-option v-for="item in regionArea" :key="item.id" :label="item.name" :value="item.id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
            </glui-row>
            <glui-row>
                <glui-col :span="8">
                    <glui-form-item label="产品经理">
                        <dropdownmanagers v-model='searchForm.ProductManagerTcNo' :clearable='true' />
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="有效性">
                        <glui-select v-model="searchForm.DataFlag" placeholder="请选择">
                            <glui-option v-for="item in dataflags" :key="item.id" :label="item.name" :value="item.id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="&nbsp;">
                        <glui-button size="small" type="warning" @click="searchRegion">
                            <i class="icon-search"></i> 查询
                        </glui-button>
                        <glui-button size="small" type="primary" @click="editRegion(0)">
                            <i class="icon-plus"></i> 新增
                        </glui-button>
                        <glui-button size="small" type="success" @click="transferRegion">
                            <i class="icon-download-alt"></i>交接
                        </glui-button>
                    </glui-form-item>
                </glui-col>
            </glui-row>
        </glui-form>
        <glui-table :data="searchResult">
            <glui-table-column prop="CityNames" label="出发地城市">
            </glui-table-column>
            <glui-table-column prop="ProductAttribute" label="产品属性">
            </glui-table-column>
            <glui-table-column prop="TcManagerName" label="同程产品经理">
            </glui-table-column>
            <glui-table-column prop="UpdateDate" label="更新时间" width="200">
            </glui-table-column>
            <glui-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <!-- {{scope.row.StartTime}}至{{scope.row.EndTime}} -->
                    <a href="javascript:void(0);" v-on:click="editRegion(scope.row.Id)">编辑</a>
                    <a href="javascript:void(0);" v-on:click="offline(scope.row.Id)">无效</a>
                </template>
            </glui-table-column>
        </glui-table>

        <glui-pagination v-on:size-change="sizeChange" v-on:current-change="pageChange" :page-sizes="[10,20]" layout="total, sizes, prev, pager, next, jumper" style="text-align:center;margin-top:14px;" :current-page="searchForm.PageIndex" :page-size="searchForm.PageSize" :total="total">
        </glui-pagination>

        <editdialog ref="editdialog" />
        <transferdialog ref="transferdialog" />
    </div>
</template>
<script>
import enums from "./enums";
import jsenums from "@/js/enums";
import httputils from "@/js/httputils";
import dropdownprovince from "@/components/dropdownprovince";
import dropdowncitybyprovince from "@/components/dropdowncitybyprovince";
import dropdownmanagers from "@/components/dropdownmanagers";
import editdialog from "./components/edit";
import transferdialog from "./components/transfer";

export default {
    components: {
        dropdownmanagers,
        dropdownprovince,
        dropdowncitybyprovince,
        editdialog,
        transferdialog
    },
    data() {
        return {
            regionArea: enums.getRegionArea(),
            productAttrs: jsenums.getProductAttribute(),
            dataflags: [{ id: 1, name: "有效" }, { id: 0, name: "无效" }],
            searchResult: [],
            total: 0,
            regionid: 0,
            searchForm: {
                CityId: "",
                ProvinceId: "",
                DataFlag: 1,
                ProductAttribute: "",
                ProductManagerTcNo: "",
                Area: "",
                PageIndex: 1,
                PageSize: 10
            }
        }
    },
    methods: {
        sizeChange: function (val) {
            this.searchForm.PageSize = val;
            this.searchRegion();
        },
        pageChange: function (val) {
            this.searchForm.PageIndex = val;
            this.searchRegion();
        },
        searchRegion() {
            let that = this;
            let loading = that.$loading({
                lock: true
            });
            httputils.get('/travel/supergoproduct/ProductRegionCharge/GetRegionChargeList',
                that.searchForm)
                .then((result) => {
                    loading && loading.close();
                    if (result) {
                        that.searchResult = result.Content;
                        that.total = result.Total;
                    }
                }).catch(err => {
                    loading && loading.close();
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
        },
        transferRegion() {
            this.$refs["transferdialog"].openDialog();
        },
        editRegion(id) {
            this.$refs["editdialog"].openDialog(id);
        },
        offline(id) {
            let that = this;
            httputils.get('/travel/supergoproduct/ProductRegionCharge/ModifyDataFlag/' + id, {}).then(result => {
                if (result && result.code > 0) {
                    that.$message({
                        message: "操作成功！",
                        type: "success"
                    });
                } else {
                    that.$confirm(result.ErrorMsg, "操作失败", {
                        confirmButtonText: "确定",
                        type: "warning",
                        center: true,
                        showCancelButton: false
                    });
                }
            }).catch(err => {

                that.$message({
                    type: 'error',
                    message: err
                });
            });
        }
    }
}
</script>




