<template>
    <glui-dialog title="产品经理编辑" :visible.sync="dialogShow" width="90%">
        <glui-form ref="editform" :model="editform" :rules="rules" label-width="100px">
            <glui-form-item prop="Area" label="所属片区">
                <glui-select v-model="editform.Area" placeholder="请选择">
                    <glui-option v-for="item in regionArea" :key="item.id" :label="item.name" :value="item.id">
                    </glui-option>
                </glui-select>
            </glui-form-item>
            <glui-form-item prop="ProductAttribute" label="产品属性">
                <glui-checkbox-group v-model="editform.ProductAttribute">
                    <glui-checkbox v-for="item in productAttrs" :key="item.id" :label="item.id">
                        {{item.name}}
                    </glui-checkbox>
                </glui-checkbox-group>
            </glui-form-item>
            <glui-form-item prop="ProvinceId" label="出发省份">
                <dropdownprovince :defaultitems='defaultProvinces' v-on:change="provincechange" v-model="editform.ProvinceId" :clearable='true' />
            </glui-form-item>
            <glui-form-item prop="CityIds" label="出发城市">
                <dropdowncitybyprovince :defaultitems='defaultCitys' v-on:change="citychange" :province="editform.ProvinceId" :city.sync='editform.CityIds' :clearable='true' :multiple='true' />
            </glui-form-item>
            <glui-form-item prop="ProductManagerTcNo" label="产品经理">
                <dropdownmanagers :defaultitems='defaultManagers' v-on:change="managerchange" v-model='editform.ProductManagerTcNo' :clearable='true' :multiple='true' />
            </glui-form-item>
            <glui-form-item prop="DataFlag" label="有效性">
                <glui-radio-group v-model="editform.DataFlag">
                    <glui-radio :label="1">是</glui-radio>
                    <glui-radio :label="0">否</glui-radio>
                </glui-radio-group>
            </glui-form-item>

            <glui-form-item>
                <glui-button size="medium" type="primary" @click="savedatas">
                    保存
                </glui-button>
            </glui-form-item>
        </glui-form>
    </glui-dialog>
</template>
<script>
import enums from "../enums";
import jsenums from "@/js/enums";
import httputils from "@/js/httputils";
import dropdownprovince from "@/components/dropdownprovince";
import dropdowncitybyprovince from "@/components/dropdowncitybyprovince";
import dropdownmanagers from "@/components/dropdownmanagers";

export default {
    components: {
        dropdownprovince,
        dropdowncitybyprovince,
        dropdownmanagers
    },
    data() {
        return {
            dialogShow: false,
            regionArea: enums.getRegionArea(),
            productAttrs: jsenums.getProductAttribute(),
            defaultManagers: [],
            defaultCitys: [],
            defaultProvinces: [],
            regionid: 0,
            editform: {
                Area: "",
                ProductAttribute: [],
                ProvinceId: "",
                ProvinceName: "",
                CityIds: [],
                CityNames: "",
                DataFlag: 1,
                ProductManagerTcNo: null,
                ProductManagerName: ""
            },
            rules: {
                Area: [{ required: true, message: "此处不可为空", trigger: "change" }],
                ProvinceId: [{ required: true, message: "此处不可为空", trigger: "change" }],
                CityIds: [{ required: true, message: "此处不可为空", trigger: "change" }],
                ProductAttribute: [{ required: true, message: "此处不可为空", trigger: "change" }]
            }
        }
    },
    methods: {
        managerchange(val, selected) {
            let that = this;
            let names = [];
            if (Array.isArray(selected)) {
                selected.forEach(i => {
                    names.push(i.Text)
                })
            } else {
                names.push(selected.Text)
            }
            that.editform.ProductManagerName = names.join(",");
        },
        citychange(val, selected) {
            let that = this;
            let names = [];
            if (Array.isArray(selected)) {
                selected.forEach(i => {
                    names.push(i.Name)
                })
            } else {
                names.push(selected.Name)
            }
            that.editform.CityNames = names.join(",");
        },
        provincechange(val, selected) {
            let that = this;
            that.editform.ProvinceName = selected.Name;
            // that.$refs["city"].getCityByProvince(val);
        },
        openDialog(id) {
            let that = this;
            that.regionid = id;
            that.dialogShow = true;
            that.loaddatas();
        },
        loaddatas() {
            let that = this;
            if (that.regionid && that.regionid >= 1) {
                let loading = that.$loading({
                    lock: true
                });
                httputils.get('/travel/supergoproduct/dsfproxy/getproduct/ProductRegionChargeApi/QueryProductRegionChargeDetail', {
                    id: that.regionid
                }).then((result) => {
                    loading && loading.close();
                    if (result) {
                        that.editform = Object.assign(that.editform, result.content);
                        that.editform.ProductAttribute = [];
                        result.content.ProductAttribute.split(',').forEach(item => {
                            that.editform.ProductAttribute.push(parseInt(item));
                        });
                        that.defaultProvinces = [{
                            Id: that.editform.ProvinceId,
                            Name: that.editform.ProvinceName
                        }];
                        let CityIds = that.editform.CityIds.split(',');
                        let CityNames = that.editform.CityNames.split(',');
                        that.defaultCitys = [];
                        let cids = [], cnames = [];
                        for (let index = 0; index < CityIds.length; index++) {
                            cids.push(parseInt(CityIds[index]));
                            cnames.push(CityNames[index])
                            that.defaultCitys.push({
                                Id: parseInt(CityIds[index]),
                                Name: CityNames[index]
                            });
                        }
                        that.editform.CityIds = cids;
                        that.editform.CityNames = cnames.join(",");
                        let ProductManagerTcNo = that.editform.ProductManagerTcNo.split(',');
                        let ProductManagerName = that.editform.ProductManagerName.split(',');
                        that.defaultManagers = [];
                        let nos = [], nnames = [];
                        for (let index = 0; index < ProductManagerTcNo.length; index++) {
                            nos.push(ProductManagerTcNo[index]);
                            nnames.push(ProductManagerName[index]);
                            that.defaultManagers.push({
                                Value: ProductManagerTcNo[index],
                                Text: ProductManagerName[index]
                            });
                        }
                        that.editform.ProductManagerTcNo = nos;
                        that.editform.ProductManagerName = nnames.join(",");
                    }
                }).catch(err => {
                    loading && loading.close();
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
            } else {
                that.editform = {
                    Area: "",
                    ProductAttribute: [],
                    ProvinceId: "",
                    ProvinceName: "",
                    CityIds: [],
                    CityNames: "",
                    DataFlag: 1,
                    ProductManagerTcNo: null,
                    ProductManagerName: ""
                };
                that.defaultManagers = [];
                that.defaultCitys = [];
                that.defaultProvinces = [];
            }
        },
        savedatas() {
            let that = this;
            this.$refs["editform"].validate(valid => {
                if (!valid) {
                    return;
                }
                let loading = that.$loading({
                    lock: true
                });
                httputils.postjson('/travel/supergoproduct/dsfproxy/postproduct/ProductRegionChargeApi/SaveProductRegionCharge', {
                    Id: that.regionid,
                    CityIds: that.editform.CityIds.join(","),
                    CityNames: that.editform.CityNames,
                    ProductAttribute: that.editform.ProductAttribute.join(","),
                    ProductManagerTcNo: that.editform.ProductManagerTcNo.join(","),
                    ProductManagerName: that.editform.ProductManagerName,
                    Area: that.editform.Area,
                    ProvinceId: that.editform.ProvinceId,
                    ProvinceName: that.editform.ProvinceName,
                    DataFlag: that.editform.DataFlag
                })
                    .then((result) => {
                        loading && loading.close();
                        if (result && result.IsSuccess) {
                            that.$message({
                                message: "保存成功！",
                                type: "success"
                            });
                        } else {
                            that.$confirm(result.ErrorMsg, "保存失败", {
                                confirmButtonText: "确定",
                                type: "warning",
                                center: true,
                                showCancelButton: false
                            });
                        }
                    }).catch((err) => {
                        that.$message({
                            type: 'error',
                            message: err
                        });
                        loading && loading.close();
                    });
            });
        }
    }
}
</script>





