<template>
    <glui-select v-model="selected" :loading="loading" :disabled="disabled" placeholder="请输入城市名称" :multiple="multiple" :clearable="clearable" filterable v-on:change="onchange" v-on:clear="clear">
        <glui-option v-for="opt in datas" :key="opt.Id" :label="opt.Name" :value="opt.Id">
        </glui-option>
    </glui-select>
</template>
<script>
import httputils from "@/js/httputils"
export default {
    data() {
        return {
            datas: [],
            selected: "",
            loading: false,
            selectdatas: []
        };
    },
    props: {
        props: {
            type: [Object],
            default() {
                return {
                    id: "Id",
                    name: "Name"
                }
            }
        },
        defaultitems: {
            type: [Array],
            default() {
                return [];
            }
        },
        province: {
            type: [String, Number, Array],
            default() {
                return "";
            }
        },
        city: {
            type: [String, Number, Array],
            default: function () {
                return "";
            }
        },
        isinner: {
            type: Boolean,
            default: function () {
                return true;
            }
        },
        multiple: {
            type: Boolean,
            default: function () {
                return false;
            }
        },
        clearable: {
            type: Boolean,
            default: function () {
                return false;
            }
        },
        disabled: {
            type: Boolean,
            default: function () {
                return false;
            }
        }
    },
    watch: {
        defaultitems(val, old) {
            let that = this;
            if (val != old) {
                let datas = [];
                let selected = that.multiple ? [] : "";
                val && val.forEach(item => {
                    if (that.multiple) {
                        selected.push(item[that.props.id]);
                    } else {
                        selected = item[that.props.id];
                    }
                    datas.push({
                        Id: item[that.props.id],
                        Name: item[that.props.name]
                    });
                });
                that.datas = datas;
                that.selected = selected;
            }
        },
        province(val, old) {
            let that = this;
            // that.selected = that.multiple ? [] : "";
            if (val !== old) {
                let pid = val;
                if (Array.isArray(val)) {
                    pid = val.join(",");
                }
                that.getCityByProvince(pid);
            }
        }
    },
    methods: {
        /**
         * 根据省份ID获取城市数据
         */
        getCityByProvince(provinceid) {
            let that = this;
            that.loading = true;
            httputils.get('/travel/supergoproduct/Common/CitiesByProvinceId', {
                id: provinceid
            }).then(resp => {
                that.loading = false;
                if (resp) {
                    that.datas = resp;
                } else {
                    that.datas = [];
                }
            }).catch(err => {
                that.loading = false;
                that.$message({
                    type: 'error',
                    message: err
                });
            });
        },
        /**
         * 选项变动回写
         */
        onchange: function (val) {
            let that = this;
            that.$emit("update:city", val);
            let selected = that.datas.concat(that.selectdatas).filter(item => {
                if (that.multiple === true) {
                    return val.findIndex(v => v == item.Id) >= 0
                } else {
                    return item && item.Id == val;
                }
            });
            that.selectdatas = selected;
            that.$emit("change", val, selected && selected.length == 1 ? selected[0] : selected);
        },
        /**
         * 清空选择项
         */
        clear: function () {
            this.datas = [];
        }
    }
}
</script>



