<template>
    <glui-dialog title="产品经理交接" :visible.sync="dialogShow" width="90%" >
        <glui-form ref="transferform" :model="transferform" :rules="rules" label-width="120px">
            <glui-form-item prop="OldJobNo" label="原始产品经理">
                <glui-input v-model="transferform.OldJobNo"></glui-input>
            </glui-form-item>
            <glui-form-item prop="NewJobNo" label="交接产品经理">
                <dropdownmanagers v-on:change="managerchange" v-model='transferform.NewJobNo' :clearable='true' />
            </glui-form-item>
            <glui-form-item>
                <glui-button size="medium" type="primary" @click="savedatas">
                    交接
                </glui-button>
            </glui-form-item>
        </glui-form>
    </glui-dialog>
</template>
<script>
import httputils from "@/js/httputils";
import dropdownmanagers from "@/components/dropdownmanagers";
export default {
    components: {
        dropdownmanagers
    },
    data() {
        return {
            dialogShow: false,
            transferform: {
                OldJobNo: "",
                NewJobNo: "",
                NewManagerName: ""
            },
            rules: {
                OldJobNo: [{ required: true, message: "此处不可为空", trigger: "change" }],
                NewJobNo: [{ required: true, message: "此处不可为空", trigger: "change" }]
            }
        }
    },
    methods: {
        openDialog() {
            let that = this;
            that.dialogShow = true;
            that.transferform = {
                OldJobNo: "",
                NewJobNo: "",
                NewManagerName: ""
            }
        },
        managerchange(val, selected) {
            let that = this;
            that.transferform.NewManagerName = selected.Text;
        },
        savedatas() {
            let that = this;
            this.$refs["transferform"].validate(valid => {
                if (!valid) {
                    return;
                }
                let loading = that.$loading({ lock: true });
                httputils.postjson('/travel/supergoproduct/dsfproxy/postproduct/ProductRegionChargeApi/SynchronizationManager',
                    that.transferform)
                    .then((result) => {
                        loading && loading.close();
                        if (result && result.IsSuccess) {
                            that.$message({
                                message: "交接成功！",
                                type: "success"
                            });
                        } else {
                            that.$confirm(result.ErrorMsg, "交接失败", {
                                confirmButtonText: "确定",
                                type: "warning",
                                center: true,
                                showCancelButton: false
                            });
                        }
                    }).catch((err) => {
                        that.$message({
                            type: 'error',
                            message: err
                        });
                        loading && loading.close();
                    });
            });
        }
    }
}
</script>





