// 产品经营方式
const ProductOperateType = [{
  id: 0,
  name: "非自营"
}, {
  id: 1,
  name: "自营"
}, {
  id: 2,
  name: "OEM" // 只有周边跟团存在OEM
}]

// 产品类型
const ProductAttribute = [{
  name: "跟团游",
  id: 0
}, {
  name: "团队",
  id: 1
}, {
  name: "快乐大巴",
  id: 2
}];

export default {
  /**
   * 获取产品经营方式
   * @param {*} belong 
   */
  getProductOperateType(belong) {
    return ProductOperateType.filter(item => {
      if (belong != 3) {
        return item.id !== 2;
      }
      return true;
    });
  },
  /**
   * 获取周边跟团产品类型
   */
  getProductAttribute() {
    return ProductAttribute;
  }
}



